html, body, .body {
        width: 100%;
        height: 100%;
        margin: 0;
}

body {
  margin: 0px;
  padding: 0px;
  font-family: 'Lato', sans-serif;
/*  background: #2580bd;*/
  color: white;
}

.connected {
  background-color: #0286D8;
  -webkit-transition: background-color 0.3s;
  -o-transition: background-color 0.3s;
  transition: background-color 0.3s;
